import React, { useState, useRef } from 'react';
import axios from 'axios';
import { Slider, Button, Select, message } from 'antd';
import { SoundOutlined, StarFilled, SearchOutlined } from '@ant-design/icons';
import ReactPlayer from 'react-player';
import { API_CONFIG } from '../config';
import ApiConfig from './ApiConfig';
import ApiConfigModal from './ApiConfigModal';

export default function TTSForm() {
  const [text, setText] = useState('');
  const [speed, setSpeed] = useState(1.0);
  const [pitch, setPitch] = useState(1.0);
  const [voice, setVoice] = useState('en-US-JennyNeural');
  const [audioUrl, setAudioUrl] = useState('');
  const [loading, setLoading] = useState(false);
  const controllerRef = useRef(null);
  const [configVisible, setConfigVisible] = useState(false);
  const [apiConfig, setApiConfig] = useState(
    JSON.parse(localStorage.getItem('azureConfig') || '{}')
  );
  const [error, setError] = useState(null);

  const VOICE_OPTIONS = [
    {
      category: '',
      pro: true,
      options: [
        { value: 'zh-CN-XiaoxiaoNeural', label: ' - ' },
        { value: 'zh-CN-YunyangNeural', label: ' - ' },
        { value: 'zh-CN-YunxiNeural', label: ' - ' },
      ]
    },
    {
      category: '',
      pro: false,
      options: [
        { value: 'zh-CN-HuihuiRUS', label: ' - ' },
        { value: 'zh-CN-Kangkang', label: ' - ' },
        { value: 'zh-CN-Yaoyao', label: ' - ' },
      ]
    },
    {
      category: '',
      pro: true,
      options: [
        { value: 'en-US-JennyNeural', label: 'Jenny - ' },
        { value: 'en-US-GuyNeural', label: 'Guy - ' },
        { value: 'en-US-AriaNeural', label: 'Aria - ' },
      ]
    },
    {
      category: '',
      pro: true,
      options: [
        { value: 'ja-JP-NanamiNeural', label: ' - ' },
        { value: 'ja-JP-KeitaNeural', label: ' - ' },
      ]
    }
  ];

  const generateSpeech = async () => {
    setError(null);
    if (!text.trim()) {
      message.warning('');
      return;
    }

    setLoading(true);
    try {
      const response = await axios.post('http://localhost:8000/api/generate', {
        text: text,
        voice: voice,
        azure_key: apiConfig.azureKey,
        azure_region: apiConfig.azureRegion || 'japaneast'
      }, {
        responseType: 'arraybuffer',  // 
        headers: {
          'Content-Type': 'application/json'  // 
        }
      });
      
      // BlobMIME
      const audioBlob = new Blob([response.data], { type: 'audio/mpeg' });
      const audioUrl = URL.createObjectURL(audioBlob);
      setAudioUrl(audioUrl);
      message.success('');
    } catch (error) {
      console.error('API:', error);
      setError('');
    } finally {
      setLoading(false);
    }
  };

  const handleSaveConfig = (values) => {
    localStorage.setItem('azureConfig', JSON.stringify(values));
    setApiConfig(values);
  };

  return (
    <div className="tts-container">
      {error && (
        <div className="error-message">
           {error}
          <button onClick={() => setError(null)}></button>
        </div>
      )}

      <Button 
        style={{ position: 'absolute', top: 20, right: 20 }}
        onClick={() => setConfigVisible(true)}
      >
        API 
      </Button>

      <ApiConfigModal
        visible={configVisible}
        onCancel={() => setConfigVisible(false)}
        onSave={handleSaveConfig}
      />

      <h1></h1>
      
      <div className="input-section">
        <textarea
          rows={6}
          value={text}
          onChange={(e) => setText(e.target.value)}
          placeholder="1000"
          maxLength={1000}
        />
      </div>

      <div className="controls">
        <div className="param-control">
          <label>:</label>
          <Slider
            min={0.5}
            max={2.0}
            step={0.1}
            value={speed}
            onChange={setSpeed}
            tipFormatter={v => v.toFixed(1)}
          />
        </div>

        <div className="param-control">
          <label>:</label>
          <Slider
            min={0.5}
            max={1.5}
            step={0.1}
            value={pitch}
            onChange={setPitch}
            tipFormatter={v => v.toFixed(1)}
          />
        </div>

        <div className="param-control">
          <label>:</label>
          <Select
            showSearch
            optionFilterProp="children"
            style={{ width: '100%', maxWidth: 500 }}
            dropdownStyle={{ maxWidth: 600 }}
            dropdownMatchSelectWidth={false}
            filterOption={(input, option) =>
              option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
            }
            options={VOICE_OPTIONS.map(group => ({
              label: (
                <div className="voice-category">
                  <span>{group.category}</span>
                  {group.pro && <span className="pro-badge">PRO</span>}
                </div>
              ),
              options: group.options.map(opt => ({
                ...opt,
                label: (
                  <div className="voice-option">
                    <span>{opt.label}</span>
                    {group.pro && <StarFilled style={{ color: '#ffc53d', marginLeft: 8 }} />}
                  </div>
                )
              }))
            }))}
            value={voice}
            onChange={setVoice}
            dropdownRender={menu => (
              <div>
                <div style={{ padding: '8px 12px', background: '#fafafa' }}>
                  <SearchOutlined /> //
                </div>
                {menu}
              </div>
            )}
          />
        </div>
      </div>

      <Button 
        type="primary" 
        onClick={generateSpeech}
        disabled={loading || !text.trim()}
      >
        {loading ? '...' : ''}
      </Button>

      {audioUrl && (
        <div className="audio-player">
          <ReactPlayer
            url={audioUrl}
            controls
            width="100%"
            height="50px"
          />
        </div>
      )}
    </div>
  );
} 