import React, { useState } from 'react';
import { Modal, Input, Button, Form, Select, Alert, Tooltip } from 'antd';
import { QuestionCircleOutlined } from '@ant-design/icons';

// 
const REGION_OPTIONS = [
  { value: 'eastus', label: ' (eastus)' },
  { value: 'eastus2', label: '2 (eastus2)' },
  { value: 'westus', label: ' (westus)' },
  { value: 'japaneast', label: ' (japaneast)' },
  { value: 'southeastasia', label: ' (southeastasia)' },
  { value: 'westeurope', label: ' (westeurope)' }
];

export default function ApiConfigModal({ visible, onCancel, onSave }) {
  const [form] = Form.useForm();
  
  const handleSubmit = () => {
    form.validateFields().then(values => {
      onSave(values);
      onCancel();
    });
  };

  const [formData, setFormData] = useState({
    azureRegion: "japaneast",
    // ...
  });

  return (
    <Modal
      title={
        <div className="config-title">
          <span>Azure API </span>
          <Button 
            type="link" 
            icon={<QuestionCircleOutlined />}
            onClick={() => window.open('https://portal.azure.com/#create/Microsoft.CognitiveServicesSpeechServices')}
          >
            
          </Button>
        </div>
      }
      visible={visible}
      onCancel={onCancel}
      footer={[
        <Button key="cancel" onClick={onCancel}></Button>,
        <Button key="submit" type="primary" onClick={handleSubmit}></Button>
      ]}
      width={600}
    >
      <Alert
        type="info"
        message=""
        description={
          <ol style={{ margin: '8px 0 0 20px' }}>
            <li>Azure<b></b></li>
            <li>84</li>
            <li></li>
          </ol>
        }
        style={{ marginBottom: 24 }}
      />

      <Form form={form} layout="vertical">
        <Form.Item
          label={
            <span>
              
              <Tooltip 
                title={
                  <div style={{ maxWidth: 300 }}>
                    <p></p>
                    <ul>
                      <li>1-120</li>
                      <li>d1f3e5g7h9j1k3m5n7p9r1t3v5x7z9</li>
                    </ul>
                  </div>
                }
              >
                <QuestionCircleOutlined style={{ marginLeft: 8 }} />
              </Tooltip>
            </span>
          }
          name="azureKey"
          rules={[
            { required: true, message: 'Azure' },
            { pattern: /^[a-zA-Z0-9]{1,120}$/, message: '1-120' }
          ]}
        >
          <Input.Password 
            placeholder="120"
            autoComplete="new-password"
          />
        </Form.Item>
        
        <Form.Item
          label=""
          name="azureRegion"
          rules={[{ required: true, message: '' }]}
          initialValue="japaneast"
        >
          <Select
            options={REGION_OPTIONS}
            placeholder=""
            optionFilterProp="label"
            showSearch
            filterOption={(input, option) =>
              option.label.toLowerCase().includes(input.toLowerCase())
            }
          />
        </Form.Item>
      </Form>
    </Modal>
  );
}

